import React,{Component} from "react";
import Ongeneral from "./Ongeneral";
import "./style.css";

class General extends Component{
    constructor(props){
        super(props);
        this.state={
            name: "",
            email: "",
            phone: "",
            condition:false,
            
        };
    }

    handleCondition=()=>{
        this.setState({
            condition:false,
        })
    }

    onSubmit=(e)=>{
        e.preventDefault();
        this.setState({
            condition:true
        })
        
    }

    nameChange=(e)=>{
        this.setState({
            name : e.target.value,

        });
    };

    emailChange=(e)=>{
        this.setState({
            email : e.target.value,

        });
    };

    phoneChange=(e)=>{
        this.setState({
            phone : e.target.value,

        });
    };

    render(){

        const {name,email,phone,condition} = this.state;
        if (!condition){
            return(
                <div className="final">
                    <h2>General Information</h2>
                    <form onSubmit={this.onSubmit}>
                        <div className="formGroup">
                          <label htmlFor="name">NAME:</label><br/>
                          <input type="text" id="name" placeholder="Enter Your Name" onChange={this.nameChange} value={this.state.name} required/><br/><br/>
                          <label htmlFor="email">E-mail:</label><br/>
                          <input type="email" id="email" placeholder="example@gmail.com" onChange={this.emailChange} value={this.state.email} required/><br/><br/>
                          <label htmlFor="phone">Phone no:</label><br/>
                          <input type="text" id="phone" placeholder="Enter Phone" onChange={this.phoneChange} value={this.state.phone} required /><br/><br/>
                        </div>
                        <div className="formGroup">
                          <button type="submit">Submit</button>
                        </div>
                        
                        
                        
                    </form>
                    
                    
                    
    
                </div>
    
            );

        }
        
        
        return(
          <Ongeneral name={name} email={email} phone={phone} condition={this.handleCondition}/>

        );
    }
}

export default General;