import React from "react";
import "./style.css";

const Ongeneral=(props)=>{
    const {name,email,phone,condition}=props;
    
    return(
        <div className="final">
             <h2>General Information</h2><br/><br/>
             <ul className="ulgeneral">
                <li>Name : {name}</li>
                <li>E-mail : {email}</li>
                <li>Phone : {phone}</li>
                
            </ul>
            <button className="button" onClick={condition}>Edit</button>
        </div>
    );

};




export default Ongeneral;