import React from "react";
import "./style.css";

const Oneducation=(props)=>{
    const {school,title,from,to,condition}=props;
    
    return(
        <div className="final">
             <h2>Education</h2><br/><br/>
             <ul className="uleducation">
                <li>School/University : {school}  </li>
                <li>Title of Study : {title} </li>
                <li>From : {from} </li>
                <li>To : {to} </li>
                
            </ul>
            <button className="button" onClick={condition}>Edit</button>
        </div>
    );

};




export default Oneducation;