import React from "react";
import "./style.css";

const Onpractical=(props)=>{
    const {company,position,from,to,main,condition}=props;
    
    return(
        <div className="final">
             <h2>Working Experience</h2><br/><br/>
             <ul className="ulpractical">
                <li>COMPANY NAME : {company}  </li>
                <li>Position Title : {position} </li>
                <li>From : {from} </li>
                <li>To : {to} </li>
                <li>Main Task : {main} </li>
                
            </ul>
            <button className="button" onClick={condition}>Edit</button>
        </div>
    );

};




export default Onpractical;