import React,{Component} from "react";
import Onpractical from "./Onpractical";
import "./style.css";

class Practicalexp extends Component{
    constructor(props){
        super(props);
        this.state={
            company:"",
            position:"",
            from:"",
            to:"",
            main:"",
            condition:false,
        }
    }
    
    handleCondition=()=>{
        this.setState({
            condition:false,
        });
    };

    conditionChange = (e)=>{
        e.preventDefault();
        this.setState({
            condition:true,
        });
    };

    companyChange=(e)=>{
        this.setState({
            company : e.target.value,
        });
    };

    positionChange=(e)=>{
        this.setState({
            position : e.target.value,
        });
    };

    fromChange=(e)=>{
        this.setState({
            from : e.target.value,
        });
    };

    toChange=(e)=>{
        this.setState({
            to : e.target.value,
        });
    };

    mainChange=(e)=>{
        this.setState({
            main : e.target.value,
        });
    };

    render(){
        const {company,position,from,to,main,condition}=this.state
        if (!condition){
        return(
            <div className="final">
                <h2>Work Experience</h2>
                <form onSubmit={this.conditionChange}>
                    <div className="formGroup">
                      <label htmlFor="company">Company Name</label><br/>
                      <input type="text" id="company" value={this.state.company} onChange={this.companyChange}  placeholder="Enter Company Name" required/><br/><br/>
                      <label htmlFor="postion">Position Title</label><br/>
                      <input type="text" id="position" value={this.state.position} onChange={this.positionChange} placeholder="Enter Job Title" required/><br/><br/>
                      <label htmlFor="from">From</label><br/>
                      <input type="date" id="from" value={this.state.from} onChange={this.fromChange} required /><br/><br/>
                      <label htmlFor="to">To</label><br/>
                      <input type="date" id="to" value={this.state.to} onChange={this.toChange} required /><br/><br/>
                      <label htmlFor="task">Main tasks:</label><br/>
                      <input type="text" id="task" value={this.state.main} onChange={this.mainChange} placeholder="Describe Your Role" required /><br/><br/>
                    </div>
                    <div className="formGroup">
                      <button type="submit">Submit</button>
                    </div>
                    
                    
                </form>
            </div>

        );
    }
    return (
        <Onpractical company={company} position={position} from={from} to={to} main={main} condition={this.handleCondition} />
    );
    
    }
}

export default Practicalexp;