import React,{Component} from "react";
import Oneducation from "./Oneducation";
import "./style.css";

class Educational extends Component{
    constructor(props){
        super(props);
        this.state={
            school:"hithere",
            title:"",
            from: " ",
            to:"",
            condition:false,
        };
    }

    handleCondition=()=>{
        this.setState({
            condition:false,
        });
    }

    conditionChange = (e)=>{
        e.preventDefault();
        this.setState({
            condition:true,
        });
    }

    schoolChange=(e)=>{
        this.setState({
            school : e.target.value,
        });
    };

    titleChange=(e)=>{
        this.setState({
            title : e.target.value,
        });
    };

    fromChange=(e)=>{
        this.setState({
            from : e.target.value,
        });
    };

    toChange=(e)=>{
        this.setState({
            to : e.target.value,
        });
    };
    render(){


        const {school,title,from,to,condition} = this.state;
        if (!condition){
        return(
            <div className="final">
                <h2>Education</h2>
                <form onSubmit={this.conditionChange}>
                    <div className="formGroup">
                      <label htmlFor="school">School/College:</label><br/>
                      <input type="text" id="school" onChange={this.schoolChange} value={this.state.school} placeholder="School/College/University" required/><br/><br/>
                      <label htmlFor="title">Title of Study:</label><br/>
                      <input type="text" id="title" value={this.state.title} onChange={this.titleChange}  placeholder="Enter Degree/Field Of Study" required/><br/><br/>
                      <label htmlFor="from">From</label><br/>
                      <input type="date" id="from" value={this.state.from} onChange={this.fromChange} required /><br/><br/>
                      <label htmlFor="to">To</label><br/>
                      <input type="date" id="to" value={this.state.to} onChange={this.toChange} required/><br/><br/>
                    </div>
                    <div className="formGroup">
                      <button type="submit">Submit</button>
                    </div>
                    
                    
                    
                </form>
            </div>

        );
    }

        return(
            <Oneducation school={school} title={title} from={from} to={to} condition={this.handleCondition}/>
        );
    }
}

export default Educational;